@font-face {
  font-family: "Uni Neue";
  src: url("../fonts/UniNeue/UniNeueBook.woff2") format("woff2"), url("../fonts/UniNeue/UniNeueBook.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MuseoSansCyrillic/MuseoSansCyrl-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  text-size-adjust: none;
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  color: #373737;
  font-size: 16px;
  line-height: 1.4;
  background-color: #f3f3f3;
}
html * {
  box-sizing: inherit;
  outline: none;
}

::-webkit-scrollbar {
  width: 8px;
  height: 0;
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: #8c8ca0;
  border-radius: 4px;
  border-right: 4px solid transparent;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

.container {
  padding-right: 16px;
  padding-left: 16px;
  max-width: 1400px;
  margin: 0 auto;
}
@media (min-width: 1025px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1280px) {
  .container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

a {
  color: #232336;
}

.header {
  background-color: #ebebeb;
  color: #232336;
  padding: 20px 0;
}
.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer {
  min-height: 300px;
  background-color: lightgray;
  padding: 60px 0;
}

.nav {
  height: 46px;
}
.nav__list {
  display: flex;
  gap: 16px;
  height: 100%;
}
.nav__list_footer {
  flex-direction: column;
  gap: 8px;
  height: fit-content;
  font-weight: bold;
}
.nav__item {
  height: 100%;
}
.nav__link {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 14px;
}
.nav__sub-menu {
  display: none;
}